import React, { FC } from "react";
import styles from "./StepTwo.module.scss";
import LabelInput from "../../../Components/LabelInput/LabelInput";
import PasswordInput from "./PasswordInput/PasswordInput";
import UnicoopButton from "../../../Components/UnicoopButton/UnicoopButton";
import { api } from "../../../API/api";
import { RegisterInfo } from "../../../interface";

type StepTwoProps = {
  studentId: number;
  major: string;
  password: string;
  isPasswordValid: boolean;
  setIsPasswordValid(e: boolean): void;
  passwordConfirm: string;
  setPasswordConfirm(e: string): void;
  onChange(name: string, value: string): void;
  setRegisterStep(e: number): void;
  changeBoxContent(): void;
  registerInfo: RegisterInfo;
};

const StepTwo: FC<StepTwoProps> = ({
  studentId,
  major,
  password,
  isPasswordValid,
  setIsPasswordValid,
  passwordConfirm,
  setPasswordConfirm,
  onChange,
  setRegisterStep,
  changeBoxContent,
  registerInfo,
}) => {
  const onClickPrevious = () => {
    setRegisterStep(1);
  };

  return (
    <div className={styles.stepTwo}>
      <LabelInput
        name={"studentId"}
        value={studentId !== 0 ? studentId : ""}
        title={""}
        placeholder={"ex) 2023311234"}
        isPassword={false}
        isReadOnly={false}
        onChange={onChange}
      />

      <LabelInput
        name={"major"}
        value={major}
        title={""}
        placeholder={"ex) "}
        isPassword={false}
        isReadOnly={false}
        onChange={onChange}
      />

      <PasswordInput
        password={password}
        isPasswordValid={isPasswordValid}
        setIsPasswordValid={setIsPasswordValid}
        passwordConfirm={passwordConfirm}
        setPasswordConfirm={setPasswordConfirm}
        onChange={onChange}
      />

      <div className={styles.privacyNotice}>
         , UNICOOP   
        .
      </div>

      <div className={styles.buttons}>
        <UnicoopButton onClick={onClickPrevious}></UnicoopButton>
        <UnicoopButton
          onClick={() =>
            api.verifyCode({
              changeBoxContent,
              isPasswordValid,
              passwordConfirm,
              registerInfo,
            })
          }
        >
          
        </UnicoopButton>
      </div>
    </div>
  );
};

export default StepTwo;
