import React, { FC } from "react";
import styles from "./StepOne.module.scss";
import UserTypeInput from "./UserTypeInput/UserTypeInput";
import LabelInput from "../../../Components/LabelInput/LabelInput";
import EmailInput from "./EmailInput/EmailInput";
import UnicoopButton from "../../../Components/UnicoopButton/UnicoopButton";
import { api } from "../../../API/api";
import { UserTypeType } from "../../../interface";

type StepOneProps = {
  userType: UserTypeType;
  name: string;
  email: string;
  verifyCode: number;
  onChange(name: string, value: string): void;
  setRegisterStep(e: number): void;
};

const StepOne: FC<StepOneProps> = ({
  userType,
  name,
  email,
  verifyCode,
  onChange,
  setRegisterStep,
}) => {
  const onClickNext = () => {
    setRegisterStep(2);
  };

  return (
    <div className={styles.stepOne}>
      <UserTypeInput userType={userType} setUserType={onChange} />

      <LabelInput
        name={"name"}
        value={name}
        title={""}
        placeholder={"ex) "}
        isPassword={false}
        isReadOnly={false}
        onChange={onChange}
      />

      <EmailInput
        email={email}
        setEmail={onChange}
        sendCode={(email) => {
          api.sendCode(email).then();
        }}
      />

      <LabelInput
        name={"verifyCode"}
        value={verifyCode !== 0 ? verifyCode : ""}
        title={""}
        placeholder={"ex) 123456"}
        isPassword={false}
        isReadOnly={false}
        onChange={onChange}
      />

      <UnicoopButton onClick={onClickNext}></UnicoopButton>
    </div>
  );
};

export default StepOne;
