import React from "react";
import styles from "./EditProfile.module.scss";
import LabelInput from "../../../Components/LabelInput/LabelInput";
import UnicoopButton from "../../../Components/UnicoopButton/UnicoopButton";
import { api } from "../../../API/api";

const EditProfile = () => {
  const email = "";

  return (
    <div className={styles.editProfile}>
      <div className={styles.row}>
        <div className={styles.title}></div>
        <LabelInput
          name={"school"}
          value={""}
          title={""}
          placeholder={""}
          isPassword={false}
          isReadOnly={true}
          onChange={() => {}}
        />
      </div>
      <div className={styles.row}>
        <div className={styles.title}></div>
        <LabelInput
          name={"studentId"}
          value={"20231234"}
          title={""}
          placeholder={""}
          isPassword={false}
          isReadOnly={true}
          onChange={() => {}}
        />
      </div>
      <div className={styles.row}>
        <div className={styles.title}></div>
        <LabelInput
          name={"major"}
          value={""}
          title={""}
          placeholder={""}
          isPassword={false}
          isReadOnly={true}
          onChange={() => {}}
        />
      </div>
      <div className={styles.row}>
        <div className={styles.title}></div>
        <LabelInput
          name={"email"}
          value={"unicoop@g.skku.edu"}
          title={""}
          placeholder={""}
          isPassword={false}
          isReadOnly={true}
          onChange={() => {}}
        />
      </div>
      <div className={styles.buttons}>
        <UnicoopButton onClick={() => {}}> </UnicoopButton>
        <UnicoopButton
          onClick={() => {
            api.deleteAccount(email).then();
          }}
        >
           
        </UnicoopButton>
      </div>
    </div>
  );
};

export default EditProfile;
